<template>
    <div class="header">
      
    </div>
</template>

<script>
export default {
    name:'Header',
     methods:{
      opensidebar(){
        document.getElementById('side-menu').style.width = '220px';
      },
      closesidebar() {
        document.getElementById('side-menu').style.width = '0';
      }
    }
}
</script>

<style>

 .header {
     height: 70px;
     width: 100%;
    display: flex;
    justify-content: flex-end;
 }

 .header a {
      margin: 20px 70px;
      font-size: 20px;
      font-weight: bold;
      color: #111;
 }

 .side-nav {
   height: 900vh;
   width: 0;
   position: fixed;
   top: 0;
   right: 0;
   background: #111;
   opacity: 0.9;
   overflow-x: hidden;
   padding: 60px 0 0 0;
   transition: 0.5s;
 }

 .side-nav a {
   padding: 10px 10px 10px 30px;
   text-decoration: none;
   font-size: 20px;
   color: #ccc;
   display: block;
   transition: 0.3s;
   cursor: pointer;
   pointer-events: all;
 }

  .side-nav a:hover {
    color: #fff;
  }

  .side-nav .btn-close {
    position: absolute;
    top: 0;
    left: 5px;
    font-size: 36px;
    margin-right: 50px;
  }

  .svg{
      position: absolute;
      top: 20px;
      display: none;
      right: 0;
  }
</style>