<template>
<div>
 <section class="container items">
     <div class="World">
         <h1>😷Total Cases</h1>
         <h3>{{totalCases}}</h3>
      </div>
      <div class="europe">
          <h1>😷 Deaths</h1>
           <h3>{{totaldeaths}}</h3>
      </div>
      <div class="asia">
         <h1>😷Recovered</h1>
          <h3>{{totalrecovered}}</h3>
      </div>
      
  </section>
<div class="container chart-wrapper">
      <chart autoresize  :options="chartOptionsBar" ></chart>
</div>
  <div class="about">
   <h1>The covid-19 stats web app</h1>
        <h1>Developed with ❤ by Rodger</h1>
        <p>To provide latest stats from various continents and countries</p>
        <div class=" social">
          <a href="https://www.instagram.com/rodger.codes/" target="_blank"><i class="fab fa-instagram fa-2x" color="#000"></i></a> 
          <a href="https://twitter.com/rodger_codes" target="_blank"><i class="fab fa-twitter fa-2x" color=""></i></a>
          <a href="mailto:rkumwanje@gmail.com" target="_blank"><i class="far fa-envelope fa-2x"></i></a>
        </div>
  </div>
        
</div>
   
</template>

<script>
import axios from 'axios';

export default {
    name:'Homesection',
    components:{
     
    },
    data() {

        let apidata = [
            this.usa,this.spain,this.italy,this.germany,this.Turkey,this.France,this.iran,this.uk,this.sw,this.Belgium
        ];
        

       return {
            
             totalCases :'',
         totaldeaths:'',
            totalrecovered:'',
             usa:'',
             italy:'',
             spain:'',
             germany:'',
             Turkey:'',
             France:'',
             iran:'',
             uk:'',
             sw:'',
             Belgium:'',

            chartOptionsBar: {
        xAxis:{
            data:["USA", "Spain", "Italy", "France", "Germany", "UK", "Iran", "Turkey", "Belgum", "Switzerland"],
            name:'Countries',
            nameTextStyle:{
                fontSize:20,
                fontWeight:'bold'
            },
            
        },
        yAxis:{
            type:'value',
            name:'Cases',
            nameTextStyle:{
                fontSize:25,
                fontWeight:'bold'
            }
        },
        series:[
            {
                type:'line',
                data:apidata
            }
        ],
        title:{
            text:'Top Ten most affected countries',
            x:'center'
        },
        responsive:true,
        mantaineAspectRatio:false
    }
            
       }
    },   
    created() {
         axios({
    "method":"GET",
    "url":"https://coronavirus-monitor.p.rapidapi.com/coronavirus/worldstat.php",
    "headers":{
    "content-type":"application/octet-stream",
    "x-rapidapi-host":"coronavirus-monitor.p.rapidapi.com",
    "x-rapidapi-key":"c753c3a7a1msh4f435c6134bbeefp111fe2jsn30d9537ade30"
    }
    })
    .then(response => {
        let {total_cases, total_deaths, total_recovered} = response.data;
        this.totalCases = total_cases;
        this.totaldeaths =total_deaths;
        this.totalrecovered = total_recovered;
    })
    .catch(err => {
        console.log(err);
    })

    axios({
    "method":"GET",
    "url":"https://coronavirus-monitor.p.rapidapi.com/coronavirus/cases_by_country.php",
    "headers":{
    "content-type":"application/octet-stream",
    "x-rapidapi-host":"coronavirus-monitor.p.rapidapi.com",
    "x-rapidapi-key":"c753c3a7a1msh4f435c6134bbeefp111fe2jsn30d9537ade30",
    "limit":"10"
    }
    })
    .then((response)=>{
        this.usa = response.data.countries_stat[1].cases 
        this.spain = response.data.countries_stat[2].cases   
        this.italy = response.data.countries_stat[3].cases 
        this.France = response.data.countries_stat[4].cases   
        this.germany = response.data.countries_stat[5].cases  
        this.uk = response.data.countries_stat[6].cases  
        this.iran = response.data.countries_stat[7].cases  
        this.Turkey = response.data.countries_stat[8].cases 
        this.Belgium = response.data.countries_stat[9].cases   
        this.sw = response.data.countries_stat[10].cases  
        console.log(response.data.countries_stat)

        
        
    })
    .catch((error)=>{
      console.log(error)
    })

    }
}
</script>

